  height: 100%;
  background-color: #000;
}
html body {
  position: relative;
  background-color: #fff;
  opacity: 1;
  transition: opacity 1s ease-out;
  will-change: opacity;
}
html body.fade {
  opacity: 0;
}
html body.overflow {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.container {
  position: absolute;
  width: 100%;
  height: 100%;
}
.container img {
  position: absolute;
}
.container img.left,
.container img.right {
  width: auto;
  height: 100%;
}
.container img.left {
  top: 0;
}
.container img.right {
  top: 0;
}
.container img.top,
.container img.bottom {
  width: 100%;
  height: auto;
}
.container img.top {
  left: 0;
}
.container img.bottom {
  left: 0;
}
.container img.map {
  left: 50%;
  top: 0;
  width: auto;
  height: 100%;
  transform: translateX(-50%);
  z-index: 2;
}
.container img.crush {
  left: 0;
  top: 0;
  opacity: 0;
  will-change: transform, opacity;
  z-index: 3;
}
.container img.pokedex {
  left: 0;
  width: 25%;
  height: auto;
  transform: translateY(100%);
  transition: transform 0.5s ease-out;
  will-change: transform;
  z-index: 4;
}
.container img.pokedex:hover {
  transform: translateY(50%);
}
.container img.appearance {
  right: 0;
  bottom: 50%;
  width: 25%;
  height: auto;
  transform: translateX(100%);
  transition: transform 1s ease-out;
  will-change: opacity, transform;
}
.container img.appearance.active {
  transition: transform 1s ease-out, opacity 1s ease-out, filter 1s ease-out 1s;
  filter: none;
}
.container img.appearance.active.caught {
  filter: brightness(0) invert(1) blur(0.5em);
  opacity: 0;
  transform-origin: 50% 50%;
  transform: scale(0);
  transition: transform 1s ease-out 1s, opacity 1s ease-out 1s, filter 1s ease-out;
}
.container img.illustration {
  left: 0;
  bottom: 0;
  width: 50%;
  height: auto;
  transform: translateX(-100%);
  transition: transform 1s ease-out;
  will-change: transform;
}
.container img.appearance.active,
.container img.illustration.active {
  transform: none;
}
.container img.tool {
  right: 12.5%;
  bottom: 25%;
  width: 6.25%;
  height: auto;
  transform: translate(50%, calc(50% + 50vh));
  transition: transform 1s ease-out;
  will-change: transform;
}
.container img.tool.active {
  transform: translate(50%, 50%);
}
.container img.tool.active:hover {
  transform: translate(50%, calc(50% + 1em));
}
.container img.tool.thrown {
  animation: throw 5s ease-in-out forwards;
}
.container .character {
  position: absolute;
  overflow: hidden;
}
.container .character,
.container .sprite {
  will-change: transform;
  z-index: 4;
}
.container .rectangles {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.container .rectangles .rectangle {
  position: absolute;
  background-color: #000;
  transition: transform linear 0.25s;
}
.container .rectangles .rectangle.left.out,
.container .rectangles .rectangle.right.out {
  width: 25%;
  height: 100%;
}
.container .rectangles .rectangle.top.out {
  width: 50%;
  height: 25%;
}
.container .rectangles .rectangle.bottom.out {
  width: 100%;
  height: 25%;
}
.container .rectangles .rectangle.left.in,
.container .rectangles .rectangle.right.in {
  width: 25%;
  height: 50%;
}
.container .rectangles .rectangle.bottom.in {
  width: 50%;
  height: 25%;
}
.container .rectangles .rectangle.left.out {
  left: 0;
  top: 0;
}
.container .rectangles .rectangle.top.out {
  left: 25%;
  top: 0;
}
.container .rectangles .rectangle.bottom.out,
.container .rectangles .rectangle.right.out {
  right: 0;
  bottom: 0;
}
.container .rectangles .rectangle.left.in {
  left: 25%;
  top: 25%;
}
.container .rectangles .rectangle.bottom.in,
.container .rectangles .rectangle.right.in {
  right: 25%;
  bottom: 25%;
}
.container .rectangles .rectangle.left.out,
.container .rectangles .rectangle.left.in {
  transform-origin: 0 0;
  transform: scaleY(0);
}
.container .rectangles .rectangle.bottom.out,
.container .rectangles .rectangle.bottom.in {
  transform-origin: 0 0;
  transform: scaleX(0);
}
.container .rectangles .rectangle.right.out,
.container .rectangles .rectangle.right.in {
  transform-origin: 0 100%;
  transform: scaleY(0);
}
.container .rectangles .rectangle.top.out {
  transform-origin: 100% 0;
  transform: scaleX(0);
}
.container .rectangles .rectangle.left.out {
  transition-delay: 1.5s;
}
.container .rectangles .rectangle.bottom.out {
  transition-delay: 1.25s;
}
.container .rectangles .rectangle.right.out {
  transition-delay: 1s;
}
.container .rectangles .rectangle.top.out {
  transition-delay: 0.75s;
}
.container .rectangles .rectangle.left.in {
  transition-delay: 0.5s;
}
.container .rectangles .rectangle.bottom.in {
  transition-delay: 0.25s;
}
.container .rectangles .rectangle.right.in {
  transition-delay: 0s;
}
.container .rectangles.active {
  z-index: 5;
}
.container .rectangles.active .rectangle {
  transform: none !important;
}
.container .rectangles.active .rectangle.left.out {
  transition-delay: 0s;
}
.container .rectangles.active .rectangle.bottom.out {
  transition-delay: 0.25s;
}
.container .rectangles.active .rectangle.right.out {
  transition-delay: 0.5s;
}
.container .rectangles.active .rectangle.top.out {
  transition-delay: 0.75s;
}
.container .rectangles.active .rectangle.left.in {
  transition-delay: 1s;
}
.container .rectangles.active .rectangle.bottom.in {
  transition-delay: 1.25s;
}
.container .rectangles.active .rectangle.right.in {
  transition-delay: 1.5s;
}
